.about-gym {
  position: relative;
  display: flex;
  justify-content: space-between;
  padding-bottom: 235px;

  @include vp-1199 {
    flex-direction: column;
    align-items: center;
    padding-bottom: 183px;
  }

  @include vp-767 {
    padding: 70px 0;
    align-items: stretch;
  }
}

.about-gym__left-column {
  max-width: 355px;
  margin-right: 30px;
  margin-left: 60px;
  padding-top: 49px;

  @include vp-1199 {
    max-width: 502px;
    margin-right: 0;
    margin-left: 0;
    padding-top: 0;
  }

  @include vp-767 {
    padding: 0 15px;
  }
}

.about-gym__title {
  margin-bottom: 6px;

  @include vp-1199 {
    text-align: center;
    margin-bottom: 8px;
  }

  @include vp-767 {
    text-align: left;
    margin-bottom: 5px;
  }
}

.about-gym__subtitle {
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  margin: 0 0 40px;

  @include vp-1199 {
    text-align: center;
    margin-bottom: 35px;
  }

  @include vp-767 {
    text-align: left;
    margin-bottom: 25px;
  }
}

.about-gym__left-column p {
  font-weight: 300;
  margin: 0 0 7px;

  @include vp-1199 {
    text-align: center;
    margin-bottom: 43px;
  }

  @include vp-767 {
    text-align: left;
    margin-bottom: 32px;
    padding-right: 15px;
  }
}

.about-gym__left-column p:last-child {
  @include vp-1199 {
    display: none;
  }
}

.about-gym img {
  object-fit: cover;

  @include vp-1199 {
    width: 678px;
    height: 345px;
  }

  @include vp-767 {
    width: 100%;
    height: 313px;
    margin-bottom: 40px;
  }
}

.about-gym__video {
  position: absolute;
  bottom: 120px;
  left: 53%;
  transform: translate(-49%, 0);
  height: 228px;
  width: 364px;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: url("../img/video-cover.jpg") center no-repeat;
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(20, 54, 122, 0.5);
    z-index: 1;
  }

  &_active {
    &::before {
      display: none;
    }

    &::after {
      display: none;
    }
  }

  &_no-js {
    &::before {
      display: none;
    }

    &::after {
      display: none;
    }
  }

  @media (min-resolution: 2dppx) {
    background-image: url("../img/video-cover@2x.jpg");
  }

  @include vp-1199 {
    bottom: 100px;
    left: 50%;
    transform: translate(-50%, 0);
    width: 266px;
    height: 167px;
  }

  @include vp-767 {
    position: relative;
    left: 0;
    bottom: 0;
    align-self: center;
    transform: none;
    width: 320px;
  }
}

.about-gym__video_active .about-gym__video-play {
  display: none;
}

.about-gym__video_no-js .about-gym__video-play {
  display: none;
}

.about-gym__video iframe {
  width: 100%;
  height: 100%;
}

.about-gym__video-play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  background: transparent url("../img/svg/video-play-button.svg") center
    no-repeat;
  border: none;
  cursor: pointer;
  transition: $trans-default;
  z-index: 2;

  &:hover,
  &:focus {
    opacity: 0.6;
  }

  &:active {
    width: 80px;
    height: 80px;
    background-size: 80%;
  }

  @include vp-1199 {
    width: 73px;
    height: 73px;
    background-size: 73px;
  }
}
